// CommentForm.js
import React, { useState, useEffect } from "react";
import axios from "axios";

const CommentForm = ({ blogId, onCommentSubmit, commentToEdit }) => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [body, setBody] = useState("");

  useEffect(() => {
    if (commentToEdit) {
      // If editing an existing comment, populate the form fields
      setName(commentToEdit.name);
      setEmail(commentToEdit.email);
      setBody(commentToEdit.body);
    }
  }, [commentToEdit]);

  const handleCommentSubmit = async (e) => {
    e.preventDefault();

    if (commentToEdit) {
      // If editing, make a PUT request to update the comment
      try {
        const response = await axios.put(
          `http://localhost:3001/api/comments/${commentToEdit.id}`,
          {
            name,
            email,
            body,
          }
        );

        // If successful, update the UI with the edited comment
        onCommentSubmit(response.data, "edit");

        // Clear the form fields
        setName("");
        setEmail("");
        setBody("");
      } catch (error) {
        console.error("Error updating comment:", error);
      }
    } else {
      // If creating a new comment, make a POST request to submit the comment
      try {
        const response = await axios.post(
          "http://localhost:3001/api/comments",
          {
            blogId,
            name,
            email,
            body,
          }
        );

        // If successful, update the UI with the new comment
        onCommentSubmit(response.data, "create");

        // Clear the form fields
        setName("");
        setEmail("");
        setBody("");
      } catch (error) {
        console.error("Error submitting comment:", error);
      }
    }
  };

  return (
    <div>
      <h3>{commentToEdit ? "Edit" : "Add"} Comment</h3>
      <form className="form-group p-2" onSubmit={handleCommentSubmit}>
        <div className="p-2">
          <input
            type="text"
            placeholder="Inter your name"
            class="form-control"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className="p-2">
          <input
            type="email"
            class="form-control"
            placeholder="Enter Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div className="p-2">
          <textarea
            value={body}
            placeholder="Inter your commont"
            class="form-control"
            onChange={(e) => setBody(e.target.value)}
            required
          />
        </div>
        <button type="submit" className="btn btn-primary">
          {commentToEdit ? "Update Comment" : "Submit Comment"}
        </button>
      </form>
    </div>
  );
};

export default CommentForm;
