import React, { useState } from "react";
import { Box, Button, InputLabel, TextField, Typography } from "@mui/material";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const CreateBlog = () => {
  const id = localStorage.getItem("userId");

  const navigate = useNavigate();
  const [inputs, setInputs] = useState({
    userId: "",
    title: "",
    body: "",
  });

  //input change
  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  //form
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/blogs", {
        userId: inputs.userId,
        title: inputs.title,
        body: inputs.body,
      });
      if (data?.success) {
        alert("Blog Created");
        navigate("/");
      }
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <>
      <form onSubmit={handleSubmit}>
        <Box
          border={3}
          borderRadius={10}
          padding={3}
          margin={"auto"}
          boxShadow={"10px 10px 20px #ccc"}
          display={"flex"}
          flexDirection={"column"}
          width={"50%"}
          marginTop={"30px"}
        >
          <Typography
            variant="h4"
            textAlign={"center"}
            fontWeight="blod"
            padding={3}
            color="gray"
          >
            create a Blog
          </Typography>
          <InputLabel
            sx={{ md: 1, mt: 2, fontSize: "24px", fontFamily: "blod" }}
          >
            userId
          </InputLabel>
          <TextField
            name="userId"
            value={inputs.userId}
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            required
          />

          <InputLabel
            sx={{ md: 1, mt: 2, fontSize: "24px", fontFamily: "blod" }}
          >
            Title
          </InputLabel>
          <TextField
            name="title"
            value={inputs.title}
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            required
          />

          <InputLabel
            sx={{ md: 1, mt: 2, fontSize: "24px", fontFamily: "blod" }}
          >
            body
          </InputLabel>
          <TextField
            name="body"
            value={inputs.body}
            onChange={handleChange}
            margin="normal"
            variant="outlined"
            required
          />
          <Button type="submit" Color="primary" variant="contained">
            Submit
          </Button>
        </Box>
      </form>
    </>
  );
};

export default CreateBlog;
