import React, { useState, useEffect } from "react";
import axios from "axios";
import { Box, Typography, Paper, Grid, Button, TextField } from "@mui/material";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";

const BlogEdit = () => {
  const [blog, setBlog] = useState({});
  const [comments, setComments] = useState([]); // State to store comments
  const { id } = useParams();
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({});

  // get blog details
  const getBlogDetail = async () => {
    try {
      const res = await axios.get(`/api/blogs/${id}`);
      setBlog(res.data.blog);
      setInputs({
        userId: res.data.blog.userId,
        title: res.data.blog.title,
        body: res.data.blog.body,
      });

      // Load comments only if they are not already loaded
      if (comments.length === 0) {
        const commentsRes = await axios.get(`/api/blogs/${id}/comments`);
        setComments(commentsRes.data.comments);
      }
    } catch (error) {
      console.log(error.message);
    }
  };

  useEffect(() => {
    getBlogDetail();
  }, [id]); // Load details whenever the ID changes

  // input change
  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };

  // form
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(`/api/blogs/${id}`, {
        userId: inputs.userId,
        title: inputs.title,
        body: inputs.body,
      });
      if (data?.success) {
        toast.success("Blog Updated");
        navigate("/");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Grid container justifyContent="center">
      <Grid item xs={12} sm={8} md={6}>
        <Paper elevation={3} style={{ padding: 20, marginTop: 20 }}>
          <Typography variant="h4" align="center" color="primary">
            Update A Post
          </Typography>
          {/* Render comments */}
          <Box marginTop={2}>
            <Typography variant="h6" gutterBottom>
              Comments:
            </Typography>
            <ul>
              {comments.map((comment) => (
                <li key={comment.id}>{comment.text}</li>
              ))}
            </ul>
          </Box>

          <form onSubmit={handleSubmit}>
            <TextField
              label="UserId"
              name="userId"
              value={inputs.userId}
              onChange={handleChange}
              fullWidth
              margin="normal"
              variant="outlined"
            />
            <TextField
              label="Title"
              name="title"
              value={inputs.title}
              onChange={handleChange}
              fullWidth
              margin="normal"
              variant="outlined"
            />
            <TextField
              label="Body"
              name="body"
              value={inputs.body}
              onChange={handleChange}
              fullWidth
              margin="normal"
              variant="outlined"
              multiline
              rows={4}
            />
            <Button
              type="submit"
              color="primary"
              variant="contained"
              style={{ marginTop: 10 }}
            >
              UPDATE
            </Button>
          </form>
        </Paper>
      </Grid>
    </Grid>
  );
};

export default BlogEdit;
