import {
  Box,
  Button,
  Card,
  CardContent,
  CardHeader,
  IconButton,
  Typography,
} from "@mui/material";
import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import ModeEditIcon from "@mui/icons-material/ModeEdit";
import DeleteIcon from "@mui/icons-material/Delete";
import FavoritesContext from "../context/FavouritesContext";
import CommentForm from "../pages/CommentForm";

const BlogDetails = () => {
  const { id } = useParams();
  const [blog, setBlog] = useState({});
  const [comments, setComments] = useState([]);
  // BlogDetails.js
  const { addFavorite, removeFavorite, favorites } =
    useContext(FavoritesContext);

  const isFavorite = favorites.some((fav) => fav._id === id);

  useEffect(() => {
    axios.get(`/api/blogs/${id}`).then((response) => setBlog(response.data));
    axios
      .get(`/api/comments/${id}`)
      .then((response) => setComments(response.data));
  }, [id]);

  const handleFavoriteToggle = () => {
    if (isFavorite) {
      removeFavorite(id);
    } else {
      addFavorite(blog);
    }
  };

  const navigate = useNavigate();
  const handleEdit = () => {
    navigate(`/blog-details/${id}`);
  };

  const handleDelete = async () => {
    try {
      const { data } = await axios.delete(`/api/blogs/${id}`);
      navigate("/");
      if (data?.success) {
        alert("Blog Deleted");
      }
    } catch (error) {
      console.log(error);
    }
  };

  const handleCommentSubmit = (newComment) => {
    // Update the UI with the new comment
    setComments([...comments, newComment]);
  };

  return (
    <div style={{ textAlign: "center" }}>
      <Card
        sx={{
          width: "40%",
          margin: "auto",
          mt: 2,
          padding: 2,
          boxShadow: "5px 5px 10px #ccc",
          ":hover:": {
            boxShadow: "10px 10px 20px #ccc",
          },
        }}
      >
        <Box display={"flex"}>
          <IconButton onClick={handleEdit} sx={{ marginLeft: "auto" }}>
            <ModeEditIcon color="info" />
          </IconButton>
          <IconButton onClick={handleDelete}>
            <DeleteIcon color="error" />
          </IconButton>
        </Box>
        <Link to={`/blog/${blog._id}`} style={{ textDecoration: "none" }}>
          <CardHeader title={blog.userId} />

          <CardContent>
            <Typography variant="h6" color="text.secondary">
              Title : {blog.title}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              Description : {blog.body}
            </Typography>
            <Button onClick={handleFavoriteToggle}>
              {isFavorite ? "Remove from Favorites" : "Add to Favorites"}
            </Button>
            <h2>Comments</h2>
            {comments.map((comment) => (
              <Typography variant="h6" color="text.secondary">
                commnet : {comment.body}
              </Typography>
            ))}
          </CardContent>
        </Link>
        <CommentForm blogId={id} onCommentSubmit={handleCommentSubmit} />
      </Card>
    </div>
  );
};

export default BlogDetails;
